import React, { useState, useRef, useEffect, useCallback } from 'react';
import {
    AppBar, Toolbar, Container, Paper, Box, List, ListItem,
    ListItemAvatar, Avatar, Typography, InputBase, IconButton,
    Chip, CircularProgress, Divider
} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import BugReportIcon from '@mui/icons-material/BugReport';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';

import { callwebService, getTenant } from '../../utils/helpers';
import { fetchCsrfToken } from '../../services/api/fetchCsrfService';
import mcp from '../../lib/mcpTools';

function App() {
    const [messages, setMessages] = useState([]);
    const [input, setInput] = useState('');
    const [loading, setLoading] = useState(false);
    const [openapiSpec, setOpenapiSpec] = useState({ paths: {}, specs: [] });
    const [showDebug, setShowDebug] = useState(false);

    const messagesEndRef = useRef(null);
    const lastApiIntentRef = useRef(null);

    /* ---------------- LOAD OPENAPI ---------------- */

    useEffect(() => {
        async function load() {
            let combined = { paths: {}, specs: [] };
            const req = require.context('../../open-apis', true, /\.json$/);
            req.keys().forEach(k => {
                const spec = req(k);
                if (spec?.paths) {
                    combined.paths = { ...combined.paths, ...spec.paths };
                    combined.specs.push(spec);
                }
            });
            setOpenapiSpec(combined);
        }
        load();
    }, []);

    /* ---------------- UTILS ---------------- */

    const scrollToBottom = useCallback(() => {
        messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    }, []);
    useEffect(scrollToBottom, [messages]);

    const resolveServerBaseForPath = async (path) => {
        for (const spec of openapiSpec.specs) {
            if (spec.paths?.[path]) {
                const server = spec.servers?.[0]?.url;
                if (server) {
                    // Replace either {3dspace} or {3DSpace} (case-insensitive variants)
                    if (/{3dspace}|{3DSpace}/i.test(server)) {
                        const tenant = await getTenant();
                        return server.replace(/\{3dspace\}|\{3DSpace\}/g, tenant).replace(/\/$/, '');
                    }
                    return server.replace(/\/$/, '');
                }
            }
        }
        // fallback: tenant as base
        return (await getTenant()).replace(/\/$/, '');
    };

    /* ---------------- LLM CALL ---------------- */

    const callAzureOpenAI = async (messages) => {
        const endpoint = 'https://genericccoehub3493163820.openai.azure.com';
        const apiKey = '***REDACTED***';
        const deployment = 'gpt-4o';

        const apiInventory = Object.entries(openapiSpec.paths).map(
            ([p, m]) => `${p}: ${Object.keys(m).join(', ').toUpperCase()}`
        ).join('\n');

        const systemPrompt = `
You are an MCP execution agent.

Available APIs:
${apiInventory}

RULES:
- If intent is executable and API exists  RETURN JSON ONLY
- Text-only answers are INVALID for executable intents
- JSON schema:
{
  "use_api": true,
  "url": "/path",
  "method": "POST|GET|PUT|PATCH|DELETE",
  "body": object | null,
  "headers": object | optional
}
Do not explain.
`;

        const res = await fetch(
            `${endpoint}/openai/deployments/${deployment}/chat/completions?api-version=2025-02-01-preview`,
            {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'api-key': apiKey
                },
                body: JSON.stringify({
                    messages: [{ role: 'system', content: systemPrompt }, ...messages],
                    temperature: 0
                })
            }
        );

        if (!res.ok) throw new Error('LLM failed');
        return res.json();
    };

    /* ---------------- EXECUTION ---------------- */

    const executeApiIntent = async (intent) => {
        let url = intent.url;
        const method = intent.method.toUpperCase();
        let body = intent.body || null;
        const headers = intent.headers || {};

        // Resolve any inline {3dspace} placeholder immediately
        if (typeof url === 'string' && (url.includes('{3dspace}') || url.includes('{3DSpace}'))) {
            try {
                const tenant = await getTenant();
                // if path uses "/{3dspace}" as prefix, prefix tenant
                if (url.includes('/{3dspace}')) {
                    url = tenant.replace(/\/$/, '') + url.replace('/{3dspace}', '');
                } else {
                    url = url.replace(/\{3dspace\}|\{3DSpace\}/g, tenant);
                }
            } catch (err) {
                console.warn('Failed to resolve {3dspace}:', err);
            }
        }

        if (url.startsWith('/')) {
            const base = await resolveServerBaseForPath(url);
            url = `${base}${url}`;
        }

        // Always ensure SecurityContext + ENO_CSRF_TOKEN are present (merge fetchCsrfToken result)
        headers.SecurityContext = headers.SecurityContext || 'VPLMProjectLeader.MyCompany.Default';
        try {
            const csrf = await fetchCsrfToken(headers.SecurityContext);
            Object.assign(headers, csrf || {});
        } catch (err) {
            console.warn('Failed to fetch CSRF token:', err);
        }

        return callwebService(method, url, body, headers);
    };

    /* ---------------- SEND MESSAGE ---------------- */

    const tryExecuteWithMcp = async (prompt) => {
        // Try to find the best operation for the prompt
        const found = mcp.findBestOperation(prompt, 1);
        if (!found) return null;
        const op = found.op;

        // Extract path parameter names from the path template
        const placeholders = [];
        const re = /\{([^}]+)\}/g;
        let m;
        while ((m = re.exec(op.path)) !== null) placeholders.push(m[1]);

        // Very simple heuristic to extract possible values from prompt: look for tokens like id:, pid:, or standalone alphanum strings
        const pathParams = {};
        const lower = prompt.toLowerCase();
        placeholders.forEach((ph, idx) => {
            // try explicit 'ph: value' or 'ph value'
            const phRegex = new RegExp(ph + "\\s*[:=]?\\s*([A-Za-z0-9_:\-@./]+)", "i");
            const match = prompt.match(phRegex);
            if (match && match[1]) {
                pathParams[ph] = match[1].trim();
                return;
            }

            // try common names like id, pid, ID, PID
            const commonRegex = new RegExp("(?:id|pid|oid)\\s*[:=]?\\s*([A-Za-z0-9_:\-@./]+)", "i");
            const cm = prompt.match(commonRegex);
            if (cm && cm[1]) {
                pathParams[ph] = cm[1].trim();
                return;
            }

            // fallback: try to pick the first long token (>=3 chars)
            const tokens = prompt.split(/[^A-Za-z0-9:_\-@./]+/).filter(Boolean);
            const candidate = tokens.find(t => t.length >= 3 && !/^(create|update|delete|get|fetch|list|an|the|a)$/.test(t.toLowerCase()));
            if (candidate) pathParams[ph] = candidate;
        });

        // Build simple body heuristic: try to parse JSON object in the prompt
        let body = null;
        const jsonMatch = prompt.match(/\{[\s\S]*\}/);
        if (jsonMatch) {
            try { body = JSON.parse(jsonMatch[0]); } catch (e) { body = null; }
        }

        // Default securityContext
        const securityContext = 'VPLMProjectLeader.MyCompany.Default';

        try {
            const result = await mcp.executeOperation(op, { pathParams, body, securityContext });
            return { op, result };
        } catch (err) {
            return { op, error: err.message };
        }
    };

    const sendMessage = async (e) => {
        e?.preventDefault();
        if (!input.trim() || loading) return;

        const userMsg = { role: 'user', content: input };
        const newMessages = [...messages, userMsg];
        setMessages(newMessages);
        setInput('');
        setLoading(true);

        // First try automatic execution using mcpTools
        try {
            const auto = await tryExecuteWithMcp(input);
            if (auto && auto.result) {
                setMessages(prev => [
                    ...prev,
                    { role: 'assistant', content: ` Auto API Executed (${auto.op.operationId}):\n\`\`\`json\n${JSON.stringify(auto.result, null, 2)}\n\`\`\`` }
                ]);
                setLoading(false);
                return;
            }
            if (auto && auto.error) {
                setMessages(prev => [...prev, { role: 'assistant', content: ` Auto-execution failed: ${auto.error}` }]);
                // continue to LLM fallback
            }
        } catch (err) {
            console.warn('Auto-execution error:', err);
        }

        /* ---- Correction handling ---- */
        if (/right api|wrong api/i.test(input) && lastApiIntentRef.current) {
            const result = await executeApiIntent(lastApiIntentRef.current);
            setMessages(prev => [
                ...prev,
                { role: 'assistant', content: ` Corrected Execution:\n\`\`\`json\n${JSON.stringify(result, null, 2)}\n\`\`\`` }
            ]);
            setLoading(false);
            return;
        }

        try {
            const modelResponse = await callAzureOpenAI(newMessages);
            const assistantMsg = modelResponse.choices[0].message;

            let apiIntent;
            try {
                apiIntent = JSON.parse(assistantMsg.content);
            } catch {
                setMessages(prev => [...prev, assistantMsg]);
                setLoading(false);
                return;
            }

            if (!apiIntent.use_api) {
                setMessages(prev => [...prev, assistantMsg]);
                setLoading(false);
                return;
            }

            lastApiIntentRef.current = apiIntent;
            const result = await executeApiIntent(apiIntent);

            setMessages(prev => [
                ...prev,
                {
                    role: 'assistant',
                    content: ` API Executed:\n\`\`\`json\n${JSON.stringify(result, null, 2)}\n\`\`\``
                }
            ]);
        } catch (err) {
            setMessages(prev => [
                ...prev,
                { role: 'assistant', content: ` Error: ${err.message}` }
            ]);
        } finally {
            setLoading(false);
        }
    };

    /* ---------------- UI ---------------- */

    return (
        <Container maxWidth="md" sx={{ height: '100vh', py: 2 }}>
            <AppBar position="static" elevation={0}>
                <Toolbar>
                    <Typography sx={{ flexGrow: 1 }}> MCP Agent</Typography>
                    <Chip label={`${Object.keys(openapiSpec.paths).length} APIs`} />
                    <IconButton onClick={() => setShowDebug(!showDebug)}>
                        <BugReportIcon />
                    </IconButton>
                </Toolbar>
            </AppBar>

            {showDebug && (
                <Paper sx={{ p: 1, my: 1 }}>
                    <IconButton onClick={() => setShowDebug(false)}>
                        <ExpandLessIcon />
                    </IconButton>
                    {Object.keys(openapiSpec.paths).map(p => (
                        <Typography key={p} variant="caption">{p}</Typography>
                    ))}
                </Paper>
            )}

            <Paper sx={{ flex: 1, mt: 2, display: 'flex', flexDirection: 'column' }}>
                <Box sx={{ flex: 1, overflowY: 'auto', p: 2 }}>
                    <List>
                        {messages.map((m, i) => (
                            <ListItem key={i}>
                                <ListItemAvatar>
                                    <Avatar>{m.role === 'user' ? 'U' : 'A'}</Avatar>
                                </ListItemAvatar>
                                <Typography sx={{ whiteSpace: 'pre-wrap' }}>{m.content}</Typography>
                            </ListItem>
                        ))}
                        {loading && <CircularProgress />}
                        <div ref={messagesEndRef} />
                    </List>
                </Box>

                <Divider />

                <Box component="form" onSubmit={sendMessage} sx={{ p: 1, display: 'flex' }}>
                    <InputBase
                        fullWidth
                        value={input}
                        onChange={e => setInput(e.target.value)}
                        placeholder="Try: create engineering item called Bolt"
                    />
                    <IconButton type="submit" disabled={loading}>
                        <SendIcon />
                    </IconButton>
                </Box>
            </Paper>
        </Container>
    );
}

export default App;
