/*
  mcpTools.js
  - Loads OpenAPI JSON files placed in src/open-apis (using webpack require.context)
  - Builds a registry of operations
  - Exposes functions to find the best operation for a natural-language prompt and execute it
  - Uses existing helpers: callwebService, getTenant and fetchCsrfToken

  Usage:
    import { findAndExecute, listOperations } from '../lib/mcpTools';
    const res = await findAndExecute('create an engineering item', { pathParams: { ID: '123' }, body: {...}, securityContext: 'role.org.collab'});

  Notes:
    - This implementation uses a lightweight scoring-based NL matcher (verb + keywords).
    - If required path parameters are missing the caller must provide them via options.pathParams.
    - Headers: it always includes CSRF token and SecurityContext via fetchCsrfToken.
*/

import { callwebService, getTenant } from "../utils/helpers";
import { fetchCsrfToken } from "../services/api/fetchCsrfService";

// Load all JSON open-api specs from src/open-apis using webpack require.context
let rawSpecs = [];
try {
  const req = require.context("../open-apis", false, /\.json$/);
  req.keys().forEach((k) => rawSpecs.push(req(k)));
} catch (e) {
  // Fallback: try to require known file(s)
  try {
    rawSpecs.push(require("../open-apis/Engineering.json"));
  } catch (err) {
    console.warn("mcpTools: no open-api specs found in src/open-apis", err);
  }
}

// Build operations registry
const registry = [];

rawSpecs.forEach((spec) => {
  const servers = spec.servers || [];
  const baseServer = servers[0] || null;
  const paths = spec.paths || {};
  Object.keys(paths).forEach((p) => {
    const pathItem = paths[p];
    Object.keys(pathItem).forEach((method) => {
      const operation = pathItem[method];
      const op = {
        method: method.toUpperCase(),
        path: p,
        operationId: operation.operationId || `${method}_${p}`,
        summary: operation.summary || "",
        description: operation.description || "",
        tags: operation.tags || [],
        parameters: operation.parameters || [],
        requestBody: operation.requestBody || null,
        servers: operation.servers || spec.servers || [],
        specTitle: spec.info && spec.info.title ? spec.info.title : "",
      };
      registry.push(op);
    });
  });
});

// Simple NLP matcher
function tokenize(text) {
  return (text || "").toLowerCase().split(/[^a-z0-9]+/).filter(Boolean);
}

function guessVerbFromPrompt(prompt) {
  const p = prompt.toLowerCase();
  if (/create|add|post|make|instantiate|new/.test(p)) return "POST";
  if (/update|patch|modify|change/.test(p)) return "PATCH";
  if (/delete|remove|destroy|drop/.test(p)) return "DELETE";
  if (/get|fetch|list|retrieve|show|read|find/.test(p)) return "GET";
  return null;
}

function scoreOperationForPrompt(op, prompt) {
  const tokens = tokenize(prompt);
  let score = 0;
  const verbGuess = guessVerbFromPrompt(prompt);
  if (verbGuess && op.method === verbGuess) score += 3;
  // match against summary, description, tags, operationId, and path
  const hay = tokenize([op.summary, op.description, op.tags.join(" "), op.operationId, op.path].join(" "));
  tokens.forEach((t) => {
    if (hay.includes(t)) score += 1;
  });
  // small bonus for exact operationId token
  const opIdTokens = tokenize(op.operationId);
  if (tokens.some((t) => opIdTokens.includes(t))) score += 2;
  return score;
}

export function listOperations() {
  return registry.map((r) => ({
    operationId: r.operationId,
    method: r.method,
    path: r.path,
    summary: r.summary,
    tags: r.tags,
  }));
}

export function findBestOperation(prompt, minScore = 1) {
  let best = null;
  let bestScore = 0;
  registry.forEach((op) => {
    const s = scoreOperationForPrompt(op, prompt);
    if (s > bestScore) {
      bestScore = s;
      best = op;
    }
  });
  if (best && bestScore >= minScore) return { op: best, score: bestScore };
  return null;
}

function fillPathParams(pathTemplate, pathParams = {}) {
  let result = pathTemplate;
  // Replace {NAME} occurrences
  result = result.replace(/\{([^}]+)\}/g, (m, p1) => {
    if (pathParams && pathParams[p1] !== undefined) return encodeURIComponent(pathParams[p1]);
    // some templates may use names like {ID} but pathParams provided in lower-case; try case-insensitive
    const foundKey = Object.keys(pathParams || {}).find((k) => k.toLowerCase() === p1.toLowerCase());
    if (foundKey) return encodeURIComponent(pathParams[foundKey]);
    // leave placeholder as-is so caller can detect missing params
    return m;
  });
  return result;
}

function buildQueryString(queryParams = {}) {
  const keys = Object.keys(queryParams || {});
  if (keys.length === 0) return "";
  return (
    "?" +
    keys
      .map((k) => `${encodeURIComponent(k)}=${encodeURIComponent(queryParams[k])}`)
      .join("&")
  );
}

async function resolveServerUrl(op) {
  // Prefer first server and substitute known variables.
  const servers = op.servers || [];
  if (!servers || servers.length === 0) return "";
  let server = servers[0];
  let urlTemplate = server.url || "";

  // If template contains {3DSpace}, replace with tenant URL from getTenant()
  if (urlTemplate.includes("{3DSpace}")) {
    try {
      const tenant = await getTenant();
      urlTemplate = urlTemplate.replace("{3DSpace}", tenant.replace(/\/$/, ""));
    } catch (e) {
      // fallback to default variable value if present
      const varDef = server.variables && server.variables["3DSpace"];
      if (varDef && varDef.default) urlTemplate = urlTemplate.replace("{3DSpace}", varDef.default.replace(/\/$/, ""));
    }
  }

  // If template contains {APIGateway} use default value if available
  if (urlTemplate.includes("{APIGateway}")) {
    const varDef = server.variables && server.variables["APIGateway"];
    if (varDef && varDef.default) urlTemplate = urlTemplate.replace("{APIGateway}", varDef.default.replace(/\/$/, ""));
  }

  return urlTemplate.replace(/\/$/, "");
}

export async function executeOperation(op, options = {}) {
  // options: { pathParams: {}, queryParams: {}, body: {}, securityContext: string }
  const { pathParams = {}, queryParams = {}, body = null, securityContext = "" } = options;

  // build base server url
  const base = await resolveServerUrl(op);
  if (!base) throw new Error("Could not resolve server URL for operation");

  let filledPath = fillPathParams(op.path, pathParams);
  // detect placeholders left unreplaced
  const missing = filledPath.match(/\{[^}]+\}/);
  if (missing) {
    throw new Error(`Missing path parameter(s) for operation: ${missing.join(",")}`);
  }

  const queryString = buildQueryString(queryParams);
  const fullUrl = base + filledPath + queryString;

  // Prepare headers - always include CSRF and SecurityContext as requested
  let headers = {};
  try {
    const csrfHeaders = await fetchCsrfToken(securityContext);
    if (csrfHeaders) headers = { ...headers, ...csrfHeaders };
  } catch (e) {
    console.warn("Failed to fetch CSRF token:", e);
  }

  // If op.parameters contain additional header defaults, you could merge here

  const resp = await callwebService(op.method, fullUrl, op.method === "GET" ? "" : body, headers);
  return resp;
}

export async function findAndExecute(prompt, options = {}) {
  // options forwarded to executeOperation when operation chosen
  const found = findBestOperation(prompt, 1);
  if (!found) {
    return { success: false, error: "No matching API operation found for prompt." };
  }
  try {
    const result = await executeOperation(found.op, options);
    return { success: true, operationId: found.op.operationId, result };
  } catch (e) {
    return { success: false, error: e.message };
  }
}

export default {
  listOperations,
  findBestOperation,
  executeOperation,
  findAndExecute,
};
